.footer:before {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  content: "";
  background-image: url("../img/ellipse-3.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: -10;
}

.footer:after {
  position: absolute;
  bottom: 150px;
  left: 0;
  content: "";
  background-image: url("../img/circle-footer.png");
  background-size: contain;
  background-repeat: no-repeat;
  width: 65%;
  height: 55%;
  z-index: -20;
}

