@import "common/footer";
@import "common/base";
@import "common/thankyou";


.top__image{
    position: absolute;
    top: -370px;
    right: -409px;
    z-index: -10;
    @media (max-width: 1279px){
        top: -153px;
        right: -370px;
    }
    @media (max-width: 767px){
        top: -85px;
        right: -130px;
    }
    img{
        @media (max-width: 1279px){
            width: 645px;
            height: 645px;
        }
        @media (max-width: 767px){
            width: 375px;
            height: 375px;
        }
    }
}

.top{
    position: relative;
    padding-top: 240px;
    overflow-x: hidden;
    padding-bottom: 290px;
    @media (max-width: 1279px){
        padding-top: 120px;
        padding-bottom: 160px;
    }
    
    &__item{
        @media (max-width: 1279px){
            margin-left: 90px;
        }
        @media (max-width: 767px){
            margin-left: 0px;
        }
        &-title{
            font-size: 72px;
            line-height: 82px;
            font-weight: 700;
            max-width: 660px;
            width: 100%;
            height: 170px;
            @media (max-width: 1279px){
                height: 95px;
            }
            @media (max-width: 767px){
                max-width: 215px;
            }
            span{
                color: #00a2ff;
            }
            @media (max-width: 1279px){
                font-size: 40px;
                line-height: 46px;
                width: 380px;
            }
            @media (max-width: 767px){
                font-size: 24px;
                line-height: 28px;
                width: 100%;
            }
        }
        &-text{
            font-size: 24px;
            line-height: 36px;
            max-width: 515px;
            width: 100%;
            margin-top: 10px;
            @media (max-width: 1279px){
                font-size: 18px;
                line-height: 27px;
                width: 400px;
            }
            @media (max-width: 767px){
                width: 100%;
                font-size: 14px;
                line-height: 21px;
            }
        }
        &-btn{
            display: flex;
            align-items: center;
            margin-top: 35px;
            position: relative;
            width: 149px;
            cursor: pointer;
            @media (max-width: 1279px){
                margin-top: 25px;
            }
            @media (max-width: 767px){
                margin-top: 15px;
            }
            a{
                font-size: 16px;
                line-height: 27px;
                color: #019BE0;
            }
            img{
                margin-left: 10px;
                position: relative;
            }
            span{
                width: 26px;
                height: 26px;
                border: 1px solid #fff;
                border-radius: 50%;
                display: inline;
                margin-left: -17px;
                position: absolute;
                top: 1px;
                right: 0;
                transition: all .4s;
            }
            &:hover{
                span{
                    width: 52px;
                    height: 52px;
                    border-radius: 50%;
                    border: 1px solid #019BE0;
                    top: -12px;
                    right: -12px;
                }
            }
            &.works-btn{
                width: 159px;
            }
            &.footer-btn{
                margin-left: auto;
                margin-top: 10px;
                width: 150px;
            }
            &.send{
                width: 96px;
                margin-left: auto;
                margin-top: 55px;
                span{
                    width: 26px;
                    height: 26px;
                    border: 1px solid #000;
                   
                }
                &:hover{
                    span{  
                        width: 52px;
                        height: 52px;
                        border-radius: 50%;
                        border: 1px solid #019BE0;
                        top: -13px;
                        right: -13px;
                    } 
                }
            }
            &.main-page{
                width: 177px;
                padding-bottom: 290px;
                // @media(max-width: 1279px){
                //     padding-bottom: 200px;
                // }
            }
        }
    } 
}


