.info{
    position: relative;
    &:before{
        content:"";
        position:absolute;
        background-image: url('../img/Ellipse-1.png');
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;
        height: 140%;
        @media (max-width: 1279px){
            width: 80%;
            height: 100%;
        }
        @media (max-width: 767px){
            z-index: -10;
            width: 100%;
            height: 100%;
        }
    }
    &__phrase{
        overflow-x: hidden;
        position: relative;
        padding-top: 490px;
        @media (max-width: 1279px){
            padding-top: 450px;
        }
        @media (max-width: 767px){
            padding-top: 150px;
        }
    }
    &__item{
        display: flex;
        justify-content: space-between;
        @media (max-width: 767px){
            flex-direction: column;

        }
        &-title{
            font-size: 40px;
            line-height: 46px;
            font-weight: 700;
            max-width: 477px;
            width: 100%;
            position: sticky;
            top: 50%;
            height: 135px;
            @media (max-width: 1279px){
                font-size: 24px;
                line-height: 28px;
                margin-right: 35px;
            }
            @media (max-width: 767px){
                max-width: 100%;
                margin-right: 0px;
                top: 0%;
                height: auto;
                position: inherit;
            }
        }
        &-text{
            max-width: 471px;
            width: 100%;
            font-size: 16px;
            line-height: 27px;
            @media (max-width: 1279px){
                font-size: 14px;
                line-height: 24px;
            }
            @media (max-width: 767px){
                max-width: 100%;
                margin-top: 20px;
            }
            p{
                margin-bottom: 35px;
                @media (max-width: 767px){
                    margin-bottom: 20px;
                }
                &:last-child{
                    margin-bottom: 0px;
                }
            }
        }
    }
}

.phrase-two{
    top: 200px;
    @media (max-width: 1279px){
        top: 230px;
    }
    @media (max-width: 767px){
        top: 160px;
    }
    img{
        @media (max-width: 1279px){
            height: 63px;
        }
        @media (max-width: 767px){
            height: 37px;
        }
    }
}


