.stages{
    padding-top: 680px;
    position: relative;
    overflow-x: hidden;
    @media (max-width: 1279px){
        padding-top: 360px;
        margin-bottom: -600px;
    }
    @media (max-width: 767px){
        padding-top: 300px;
    }
    &__item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 540px;
        @media (max-width: 1420px){
            justify-content: space-evenly;
        }
        @media (max-width: 1279px){
            flex-direction: column;
        }
        &-block{
            position: relative;
            @media (max-width: 1279px){
                margin-left: -353px;
                margin-bottom: 230px;
            }
            @media (max-width: 767px){
                margin-left: -4px;
                margin-bottom: 350px;
            }
            .stages__item-block-circle{
                animation: rotate 5s linear infinite;
                @keyframes rotate{
                    from {transform: rotate(0deg)}
                    to {transform: rotate(360deg)}
                }
            }
            .stages__item-block-number{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
            &__info{
                width: 277px;
                height: 277px;
                border-radius: 50%;
                border: 1px solid #646482;
                padding-top: 65px;
                padding-left: 55px;
                padding-right: 55px;
                position: absolute;
                left: -72.5px;
                @media (max-width: 1279px){
                    left: 105px;
                }
                @media (max-width: 767px){
                    left: -70px;
                }
                &.one{
                    bottom: 115px;
                    @media (max-width: 1279px){
                        bottom: -70px;
                    }
                    @media (max-width: 767px){
                        bottom: -250px;
                    }
                }
                &.two{
                    top: 115px;
                    @media (max-width: 1279px){
                        top: -75px;
                    }
                    @media (max-width: 767px){
                        top: 105px;
                    }
                }
                &.three{
                    bottom: 115px;
                    @media (max-width: 1279px){
                        bottom: -70px;
                    }
                    @media (max-width: 767px){
                        bottom: -245px;
                    }
                }
                &.four{
                    top: 115px;
                    @media (max-width: 1279px){
                        top: -70px;
                    }
                    @media (max-width: 767px){
                        top: 105px;
                    }
                }
                &-title{
                    font-size: 18px;
                    line-height: 20px;
                    font-weight: 700;
                }
                &-text{
                    font-size: 12px;
                    line-height: 18px;
                    font-weight: 300;
                    margin-top: 7px;
                }
            }
            
        }
    }
    &__arrow{
        @media (max-width: 1279px){
            transform: rotate(90deg);
            position: absolute;
            &.one{
                top: 600px; 
            }
            &.two{
                top: 970px;
            }
            &.three{
                top: 1340px;
            }
        }
        @media (max-width: 767px){
            &.one{
                top: 710px;   
            }
            &.two{
                top: 1195px;
            }
            &.three{
                top: 1680px;
            }
        }
    }
}

.team{
    position: relative;
    padding-bottom: 410px;
    color: #fff;
    @media (max-width: 1279px){
        margin-top: 50px;
        padding-bottom: 240px;
    }
    &:before{
        content:"";
        position: absolute;
        background-image: url('../img/Ellipse-1.png');
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;
        height: 140%;
        top: -80px;
        left: 0;
        @media (max-width: 1279px){
            width: 80%;
            height: 100%;
        }
        @media (max-width: 767px){
            z-index: -10;
            width: 100%;
            height: 100%;
        }
    }
    &__phrase{
        overflow-x: hidden;
        position: relative;
        padding-top: 360px;
        @media (max-width: 1279px){
            padding-top: 160px;
        }
        @media (max-width: 767px){
            padding-top: 90px;
        }
    }
    &__block{
        display: flex;
        justify-content: space-between;
        @media (max-width: 990px){
            flex-direction: column;
        }
        &-title{
            font-size: 24px;
            line-height: 36px;
            font-weight: 700;
            
        }
        &-location{
            z-index: 10;
            @media (max-width: 1279px){
                margin-left: 100px;
            }
            @media (max-width: 990px){
                margin-left: 80px;
            }
            @media (max-width: 600px){
                margin-left: 0px;
            }
            &-wrapper{
                display: flex;
                margin-top: 25px;
                @media (max-width: 500px){
                    flex-direction: column;
                }
            }
            &-figure{
                width: 170px;
                height: 170px;
                border-radius: 50%;
                border: 1px solid #646482;
                position: relative;
                &:first-child{
                    margin-right: 10px;
                }
                p{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 16px;
                    font-weight: 300;
                }
                @media (max-width: 500px){
                    margin: 0 auto;
                    &:first-child{
                        margin: 0 auto;
                        margin-bottom: 10px;
                    }
                }
            }
        }
        &-item{
            margin-right: 100px;
            z-index: 10;
            @media (max-width: 990px){
                margin-left: 80px;
                margin-top: 65px;
            }
            @media (max-width: 767px){
                margin-right: 0px;
            }
            @media (max-width: 600px){
                margin-left: 0px;
            }
            @media (max-width: 500px){
                margin-top: 230px;
            }
            ul{
                margin-top: 15px;
                @media (max-width: 990px){
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    height: 190px;
                }
                @media (max-width: 500px){
                    height: auto;
                }
                li{
                    font-size: 16px;
                    line-height: 56px;
                    font-weight: 300;
                    padding-left: 22px;
                    position: relative;
                    @media (max-width: 767px){
                        font-size: 14px;
                        line-height: 49px;
                    }
                    &:before{
                        position: absolute;
                        content: "";
                        width: 10px;
                        height: 10px;
                        border: 1px solid #646482;
                        border-radius: 50%;
                        left: 0;
                        top: 50%;
                        transform: translate(0, -50%);
                    }
                }
            }
        }
    }
}

