                transform: translate(-50%, -50%);
            }
            @media (max-width: 1279px){
                width: 169px;
                height: 169px;
                margin-left: 10px;
                margin-right: 10px;
                img{
                    width: calc(100% * .5);
                }
            }
            @media (max-width: 767px){
                width: 124px;
                height: 124px;
                margin-left: 6px;
                margin-right: 6px;
                img{
                    width: calc(100% * .4);
                }
            }
        }
    }
}


.footer{
    padding-top: 640px;
    position: relative;
    &:before{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
        content: "";
        background-image: url("../img/ellipse-3.png");
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
        z-index: -10;
    }
    &:after{
        position: absolute;
        bottom: 150px;
        left: 0;
        content: "";
        background-image: url("../img/circle-footer.png");
        background-size: contain;
        background-repeat: no-repeat;
        width: 65%;
        height: 55%;
        z-index: -20;
        @media (max-width: 1000px){
            bottom: 50px;
        }
    }
    @media (max-width: 1279px){
        padding-top: 430px;
    }
    @media (max-width: 767px){
        padding-top: 250px;
    }
    &__wrapper{
        display: flex;
        justify-content: space-between;
        @media (max-width: 767px){
            flex-direction: column;
        }
    }
    &__title{
        width: 540px;
        @media (max-width: 767px){
            width: 100%;
        }
        h2{
            font-size: 72px;
            @media (max-width: 1279px){
                font-size: 40px;
            }
            @media (max-width: 767px){
                font-size: 24px;
                margin-bottom: 20px;
            }
        }
        span{
            font-size: 24px;
            line-height: 28px;
            @media (max-width: 1279px){
                font-size: 18px;
                line-height: 20px;
                
            }
            @media (max-width: 767px){
                font-size: 14px;
            }
        }
    }
    &__item{
        max-width: 585px;
        width: 100%;
        margin-bottom: 405px;
        margin-top: 170px;
        @media (max-width: 1279px){
            margin-bottom: 230px;
        }
        @media (max-width: 767px){
            max-width: 100%;
            margin-bottom: 125px;
        }
        &-name,
        &-mail,
        &-about-project{
            width: 100%;
            font-size: 21px;
            line-height: 40px;
            background-color: transparent;
            outline: none;
            border: none;
            border-bottom: 1px solid grey;
            margin-bottom: 45px;
            padding-left: 10px;
            padding-right: 10px;
            color: grey;
            @media (max-width: 1279px){
                font-size: 14px;
            }
        }
    }
    &__bottom{
        line-height: 70px;
        background-color: #010119;
        text-align: center;
        font-size: 12px;
    }
}


